	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p><b>Note:</b> In order to configure your Jira integration, you must be an admin on Jira in order to properly have webhooks installed. After your integration has properly been set up, you can go back to regular access on Jira.  </p>\n</blockquote>\n<p>You can integrate with Jira Server in order to sync information between your HackerOne report and your Jira issue. </p>\n<p>To integrate with Jira Server:</p>\n<ol>\n<li>\n<p>Go to <strong>Settings > Program > Integrations</strong>.</p>\n</li>\n<li>\n<p>Click the <strong>Connect with Jira</strong> link.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-aefd5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 8.739255014326648%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAYAAABYBvyLAAAACXBIWXMAABYlAAAWJQFJUiTwAAAAVUlEQVQI13WMQQ6AMAgE+/+nmkhASrVFu9Ke7EGSzWyyZJJawcYF43rvk8UMzAxVhYjA/V72b3+C+awzWiqSe4vSlifLGbTvU0pEIfRf4eBhFyQy+AKWGZzJ0lfT9QAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-1\"\n        title=\"\"\n        src=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-2f183.png\"\n        srcset=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-67829.png 125w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-5c59f.png 250w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-2f183.png 500w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-c93e8.png 750w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-4e628.png 1000w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-aefd5.png 1396w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Choose <strong>Jira Server</strong>, and you'll be redirected to this 4-step setup screen where you can configure your Jira integration.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-53fa9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.56451612903226%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-3\"\n        title=\"\"\n        src=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-2f183.png\"\n        srcset=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-67829.png 125w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-5c59f.png 250w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-2f183.png 500w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-c93e8.png 750w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-53fa9.png 992w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-5bd7d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 76.52645861601086%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-2\"\n        title=\"\"\n        src=\"/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-2f183.png\"\n        srcset=\"/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-67829.png 125w,\n/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-5c59f.png 250w,\n/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-2f183.png 500w,\n/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-c93e8.png 750w,\n/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-4e628.png 1000w,\n/static/jira-server-2-41203532d0e8b46e3418ae97cb07c049-5bd7d.png 1474w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Navigate to the Application Links console of Jira by going to <strong>Jira Administration > Applications > Application Links</strong>.</p>\n</li>\n</ol>\n<blockquote>\n<p><strong>Note:</strong> You must be a Jira administrator in order to set up and access the Jira integration settings. After you set up your integration, you can go back to regular access on Jira.</p>\n</blockquote>\n<p>   \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-4e7b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.296875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-7\"\n        title=\"\"\n        src=\"/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-2f183.png\"\n        srcset=\"/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-67829.png 125w,\n/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-5c59f.png 250w,\n/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-2f183.png 500w,\n/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-c93e8.png 750w,\n/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-4e628.png 1000w,\n/static/jira-server-7-a28bde44fb0c895a7a7a98f3c806fbd6-4e7b7.png 1280w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"5\">\n<li>\n<p>Enter <code>https://hackerone.com</code> and click <strong>Create new link</strong>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-99d7e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.49648711943794%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-8\"\n        title=\"\"\n        src=\"/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-2f183.png\"\n        srcset=\"/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-67829.png 125w,\n/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-5c59f.png 250w,\n/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-2f183.png 500w,\n/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-c93e8.png 750w,\n/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-4e628.png 1000w,\n/static/jira-server-8-183741fb7ae19fbb049e7a46eaba6b02-99d7e.png 1281w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Enter <code>https://hackerone.com</code> in the New URL field in the Configure Application URL window.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-4e7b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 59.84375000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-9\"\n        title=\"\"\n        src=\"/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-2f183.png\"\n        srcset=\"/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-67829.png 125w,\n/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-5c59f.png 250w,\n/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-2f183.png 500w,\n/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-c93e8.png 750w,\n/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-4e628.png 1000w,\n/static/jira-server-9-e3824ba7660c59482aae1b6c2640e33d-4e7b7.png 1280w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Enter <code>HackerOne</code> for the Application Name and select <code>Generic Application</code> in the Link applications window and click <strong>Continue</strong>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-99d7e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 71.35050741608119%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-10\"\n        title=\"\"\n        src=\"/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-2f183.png\"\n        srcset=\"/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-67829.png 125w,\n/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-5c59f.png 250w,\n/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-2f183.png 500w,\n/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-c93e8.png 750w,\n/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-4e628.png 1000w,\n/static/jira-server-10-f976d00911eeb479f49c416d75d5be1d-99d7e.png 1281w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Select the pencil icon for HackerOne on the Configure Application Links page.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-4e7b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35.46875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-11\"\n        title=\"\"\n        src=\"/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-2f183.png\"\n        srcset=\"/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-67829.png 125w,\n/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-5c59f.png 250w,\n/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-2f183.png 500w,\n/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-c93e8.png 750w,\n/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-4e628.png 1000w,\n/static/jira-server-11-9d878997551dbace2a7888e8cb1caea9-4e7b7.png 1280w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n<li>\n<p>Enter the following information on the Application Details section of the Configure HackerOne window:</p>\n</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Field</th>\n<th>Details</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Application Name</td>\n<td>HackerOne</td>\n</tr>\n<tr>\n<td>Display URL</td>\n<td><a href=\"https://hackerone.com\">https://hackerone.com</a></td>\n</tr>\n</tbody>\n</table>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-4e7b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 63.98437500000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-12\"\n        title=\"\"\n        src=\"/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-2f183.png\"\n        srcset=\"/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-67829.png 125w,\n/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-5c59f.png 250w,\n/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-2f183.png 500w,\n/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-c93e8.png 750w,\n/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-4e628.png 1000w,\n/static/jira-server-12-a2d6d97295c473a95b72879e374b19f8-4e7b7.png 1280w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"10\">\n<li>Enter the following information on the Incoming Authentication section of the Configure HackerOne window:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Field</th>\n<th>Details</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Consumer Key</td>\n<td>HackerOne Jira Client</td>\n</tr>\n<tr>\n<td>Consumer Name</td>\n<td>HackerOne</td>\n</tr>\n<tr>\n<td>Public Key</td>\n<td>Copy and paste from the key below:\n<br>\n<br>\n-----BEGIN PUBLIC KEY----\n<br>\nMIICIjANBgkqhkiG9w0BAQEFAAOCAg8AMIICCgKCAgEAy8H4gLqU2Lkkce11/Scn\n<br>\nzac5uLs4Wa3xkg8pwTJb4AaUXxPp5c49MVXRlGhLMeQ2Wv1AfNvxJu4gcmvUyn6Q\n<br>\nA9hD156grA7qXD3G7aX4DJ4N5ialNaDQGZCWRqxAqwrT7KoXZohbiima5bAyUjgG\n<br>\nSwdGKTIHp7djBNfWa8Wv9uDbJZkNRkhzcuJ65dmsPk0gURosCzo3FhL9e0kjVt66\n<br>\nGbTMjP02L0BZYpFhwTbHTsrw/xpmW9RRDGZ9jYNXbCeki17bYdVSdGswvamvzv5Y\n<br>\n4EBK4hV4ocs+MumEWC5lYn1zdWjXaQcDTnrQLgF1BJ//hB7l6nPSrynYCiM4ceCU\n<br>\nvo1/2p6nOvNvbBjVlRMIYbsrrUgaBT1tvdHRqTkBDpxzDEQcNBuEgxhxu7oHNDvK\n<br>\nHnZsorPPvfixqhpB3jcO9NuYujPakAV/ishjrwwQtRIRXqLAFHYYQxbzfikhi0xs\n<br>\nRJGg/kzpkmz9F93cATONpMFSVrg6a4Vxmt0Yt8zND2WNi/8qHCWN2yju2DUWLLRm\n<br>\n6MdAkz48MMTCmYXRybB+6g8a9F4YBdl2TGnWsYfQ1R/y0utRKBC+P7cQwVTP8eus\n<br>\nliA2Ts+KvYyVgD6StR629HFyNCNglm9+NuudtYdUUfIHGlEq769wLwHX3bgddqBW\n<br>\nhgko43a2w0IWTvNkga5FBcMCAwEAAQ==\n<br>\n-----END PUBLIC KEY-----</td>\n</tr>\n</tbody>\n</table>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-4e7b7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.81249999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-13\"\n        title=\"\"\n        src=\"/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-2f183.png\"\n        srcset=\"/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-67829.png 125w,\n/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-5c59f.png 250w,\n/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-2f183.png 500w,\n/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-c93e8.png 750w,\n/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-4e628.png 1000w,\n/static/jira-server-13-0481310f533ffc4e92f1b768b617a878-4e7b7.png 1280w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"11\">\n<li>Click <strong>Save</strong>.</li>\n<li>Navigate back to HackerOne and enter the URL of the Jira instance you want to authenticate with in the <strong>Instance base URL</strong> field in the <strong>Configure an Application Link</strong> step.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-0d5a0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35.29411764705882%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-14\"\n        title=\"\"\n        src=\"/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-2f183.png\"\n        srcset=\"/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-67829.png 125w,\n/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-5c59f.png 250w,\n/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-2f183.png 500w,\n/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-c93e8.png 750w,\n/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-4e628.png 1000w,\n/static/jira-server-14-3a7c1e20c5a5e75582e621a389e518f4-0d5a0.png 1326w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>Click the <strong>Authenticate with Jira</strong> button to finalize the setup between HackerOne and your Jira instance.</li>\n<li>\n<p>Configure which HackerOne projects you'd like to escalate to Jira in the <strong>Configure HackerOne to Jira escalation</strong> window.</p>\n<ul>\n<li>Select the <strong>Allow me to select a project every time when I escalate an issue</strong> checkbox to have the ability to select from multiple projects when creating Jira issues.</li>\n<li>Select the Jira Priority you want to map to the corresponding HackerOne severity. This enables the right priority to be set when escalating a HackerOne report to Jira.</li>\n</ul>\n</li>\n</ol>\n<p>   \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-aa501.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 92.28508449669361%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-4a\"\n        title=\"\"\n        src=\"/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-2f183.png\"\n        srcset=\"/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-67829.png 125w,\n/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-5c59f.png 250w,\n/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-2f183.png 500w,\n/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-c93e8.png 750w,\n/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-4e628.png 1000w,\n/static/jira-server-hackerone-to-jira-5f3a21470784a119f9a9bbaa7165c00d-aa501.png 1361w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"15\">\n<li><em>(Optional)</em> Select which Jira actions you'd like to post onto HackerOne in the <strong>Select Jira to HackerOne events</strong> window. You can choose from:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Status changed</td>\n<td>Post an internal comment when an issue changes status.</td>\n</tr>\n<tr>\n<td>Resolution changed</td>\n<td>Post an internal comment when an issues changes resolution.</td>\n</tr>\n<tr>\n<td>Priority changed</td>\n<td>Post an internal comment when an issue changes priority.</td>\n</tr>\n<tr>\n<td>Assignee changed</td>\n<td>Post an internal comment when an issue changes assignee.</td>\n</tr>\n<tr>\n<td>Comment added</td>\n<td>Post an internal comment when someone comments on an issue.</td>\n</tr>\n<tr>\n<td>Closed issue status</td>\n<td>Resolve the report when an issue is closed with the selected status.</td>\n</tr>\n</tbody>\n</table>\n<p>   \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-6e368.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.18518518518519%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-5\"\n        title=\"\"\n        src=\"/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-2f183.png\"\n        srcset=\"/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-67829.png 125w,\n/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-5c59f.png 250w,\n/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-2f183.png 500w,\n/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-c93e8.png 750w,\n/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-4e628.png 1000w,\n/static/jira-server-jira-to-hackerone-f21023f25f6ba7905c7e34cda4030610-6e368.png 1350w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"16\">\n<li><em>(Optional)</em> Select which actions in HackerOne you'd like to post to Jira in the <strong>Select HackerOne to Jira events</strong> window. You can choose from:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Comments</td>\n<td>When someone comments on a report, post an update on the associated Jira issue.</td>\n</tr>\n<tr>\n<td>State Changes</td>\n<td>When someone changes the state of a report, post an update on the associated Jira issue.</td>\n</tr>\n<tr>\n<td>Rewards</td>\n<td>When someone awards or suggests a bounty and/or bonus, post an update on the associated Jira issue.</td>\n</tr>\n<tr>\n<td>Assignee Changes</td>\n<td>When someone assigns a user/group to a report, post an update on the associated Jira issue.</td>\n</tr>\n<tr>\n<td>Disclosure</td>\n<td>When disclosure is requested or a report becomes public, post an update on the associated Jira issue.</td>\n</tr>\n<tr>\n<td>Synchronize attachments</td>\n<td>Synchronize attachments linked with reports and comments to the associated Jira issue.</td>\n</tr>\n</tbody>\n</table>\n<p>   \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-2f286.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 73.42657342657343%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-6a\"\n        title=\"\"\n        src=\"/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-2f183.png\"\n        srcset=\"/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-67829.png 125w,\n/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-5c59f.png 250w,\n/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-2f183.png 500w,\n/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-c93e8.png 750w,\n/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-4e628.png 1000w,\n/static/jira-server-6a-ed2fa6b4073319e4fc3e2b21d9a499b5-2f286.png 1430w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>You're all set! Now that you've finished setting up the Jira integration, you can create Jira issues right from your HackerOne report.</p>","frontmatter":{"path":"/programs/jira-server-integration.html","id":"programs/jira-server-integration","title":"Jira Server Integration Setup","description":null}}},"pathContext":{}}

